.pop-wrap {display:none;position:fixed;left:0;top:0;width:100%;height:100%;background:rgba(0,0,0,.3);z-index:9000;opacity:0;border-top:solid 1px $line-color;
	.pop-content {position:absolute;left:50%;top:50%;background:#fff;box-shadow:$default-shadow;@include transform(scale(0));
		.pop-header {height:50px;background:$table-header-bg;position:relative;
			h3 {padding:0 17px;line-height:50px;}
			.pop-close {position:absolute;display:block;top:16px;right:20px;@include sprite($cross);border:none;background-color:transparent;text-indent:-1000px;overflow:hidden;}
		}
		.pop-body {padding:14px 10px;}
	}

	&.ready {display:block;}
	&.open {opacity:1;@include transition(opacity $popupOpenDuration);
		.pop-content {@include transform(scale(1));@include transition(transform $popupOpenDuration);}
	}
	&.close {opacity:0;@include transition(opacity $popupCloseDuration);
		.pop-content {@include transform(scale(4));@include transition(transform $popupCloseDuration );}
	}
}