//--------------------------------------- old ------------------------------------------

html {height:100%;}
body {color:$default-color;min-height:100%;line-height:1.7;/* letter-spacing:-.05em; */font-family: "NanumGothic","sans-serif"; font-size: 12px;}

//link
a {color: $default-color;}
a:hover, a:focus {color:inherit; text-decoration:underline;}

//form
input, select, textarea {padding-left:8px;padding-right:8px;border:solid 1px $line-color;outline:none;@include transition(border-color $transitionDuration);
	&:focus, &:active {border-color:$primary-color;}
	&::placeholder {color:$muted-color;}
}
input {
	&[type=text], &[type=password], &[type=email], &[type=number] {width:160px;height:30px;}
}
select {width:160px;height:30px;padding-right:0;}
input[type=radio], input[type=checkbox] {vertical-align:middle;}
label {font-weight:normal;}

//button
.btn-normal, .btn-point, .btn-error {display:inline-block;min-width:82px;padding:0 10px;border:none;color:#fff;text-align:center;height:30px;line-height:30px;outline:none;@include transition(background $transitionDuration);
	&:hover, &:focus {text-decoration:none;outline:none;}
	&.big {font-size:18px;min-width:150px;height:50px;padding-left:30px;padding-right:30px;line-height:50px;}
}
.btn-normal {background:#fff;color:$default-color;border:solid 1px $primary-color;
	&:hover, &:focus {background:$table-header-bg;}
}
.btn-point {background:$primary-color;border:solid 1px $primary-color;
	&:hover, &:focus {color:#fff;background:$primary-color-focus;border:solid 1px $primary-color-focus;}
}
.btn-error {background:$error-color;border:solid 1px $error-color;
	&:hover, &:focus {color:#fff;background:$error-color-focus;border:solid 1px $error-color-focus;}
}
//파일 input
.file_input label {cursor:pointer;display:inline-block;min-width:82px;padding:0 10px;height:30px;line-height:30px;background:$primary-color;color:#fff;text-align:center;
	&:hover, &:focus {background:$primary-color-focus;}
	input {position:absolute;width:0;height:0;overflow:hidden;border:none;}
}
.button-box {text-align:center;margin-top:60px;}
.btnArea {margin-top:20px;text-align:right; }

//help tooltip
.help-tooltip {@include sprite($ico-help);display:inline-block;cursor:pointer;vertical-align:middle;}

//util
.image-box {background-size:contain;background-repeat:no-repeat;background-position: center;}

//컨텐츠 배너
.conBanner {min-height:194px;
	.txt {float:left;line-height:1.8;margin:40px 0 0 0;
		h2 {font-size:24px;}
		strong{font-size:18px;font-weight:600;line-height:30px;}
		p{font-size:14px;line-height:28px;}
	}
	span img{max-width:120px; margin:50px 0 0 0;float:right;}
	&:after {content:'';display:block;float:none;clear:both;}
}

//타이틀
h3 {font-size:18px;font-weight:600;margin-bottom:6px;}
.sub-title {
	h3 {float:left;}
	.description {float:right;padding-top:5px;}
	ul {float:none;clear:both;width:100%;margin-bottom:6px;
		li {padding-left:7px;position:relative;
			&:after {content:'';position:absolute;width:4px;height:1px;background:$default-color;left:0;top:50%;}
		}
	}
	&:after {content:'';display:block;float:none;clear:both;}
}


//테이블
table {
	&.list {width:100%;
		border-top:solid 1px $table-line-color;border-bottom:solid 1px $table-line-color;
		>tbody, >thead {
			>tr {
				>th {background:$table-header-bg;}
				>th, >td {padding:6px 10px;font-weight:normal;}
				&.border-b {
					>th, >td {border-bottom:solid 1px $table-line-color;}
				}
			}
		}

		.pop-content & {border-top:none;border-bottom:none;
			>tbody, >thead {
				>tr {
					>th {background:none;}
				}
			}
		}
	}
}

//게시판
.boardView {
	.detail {
		p {line-height:24px;}
		img {max-width:100%;}
		a {text-decoration:underline;
			&:visited {color:$visited-color;}
		}
	}
}


//페이징
.paging {height:25px; margin-top:60px; text-align:center;
	a {display:inline-block;border:solid 1px transparent;padding:0 5px;line-height:21px;vertical-align:top;
		&.on {font-weight:bold;text-decoration:underline;}
		&.prev, &.next {border:solid 1px $line-color;width:24px;height:24px;position:relative;
			&:after {content:'';position:absolute;left:50%;top:50%;margin-top:-6px;margin-left:-4.5px;}
			img {display:none;}
			&:hover, &:active {border:solid 1px $primary-color;}
		}
		&.prev {margin-right:6px;
			&:after {@include sprite($arrow-left-paging);}
			&:hover:after {@include sprite($arrow-left-paging-on);}
		}
		&.next {margin-left:6px;
			&:after {@include sprite($arrow-right-paging);}
			&:hover:after {@include sprite($arrow-right-paging-on);}
		}
	}
}

//약관
.clause, .clause2 {width: 100%; height: 120px; overflow-y: scroll; border: 1px solid $line-color; margin: 10px 0 0 0; padding: 5px 0 5px 5px;}
.clause img{max-width: 570px;}
.clause2 img{max-width:800px;}
