.header-wrap {background:url(../imgs/rentking.w/header_bg.jpg) no-repeat center top;background-size:100% auto;position:relative;z-index:3000;box-shadow:$default-shadow;min-width:940px;
	header {width:100%;color:#fff;position:relative;z-index:2000;
		.inner {width:940px;margin:0 auto;height:76px;position:relative;
			a {outline:none;}
			h1 {color:transparent;text-indent:-4000px;width:218px;height:40px;background:url(../imgs/rentking.w/title.png) no-repeat center;position:absolute;left:0;top:18px;}
			ul.menu {position:absolute;top:0;left:260px;height:100%;
				li {display:inline-block;position:relative;
					a { color: #fff;text-decoration:none;font-size:16px;font-weight:600;display:block;padding:0 26px;line-height:76px;
						&:before {content:'';display:block;height:1px;background:#fff;position:absolute;left:100px;right:100px;bottom:0;@include transition(left $transitionDuration, right $transitionDuration);}
						&:hover:before, &:active:before, &:focus:before {left:0;right:0;}
					}
					&:hover a:before {left:0;right:0;}

					ul {position:absolute;width:100%;margin-top:10px;overflow:hidden;height:0;@include transition(height $transitionDuration);
						li {width:100%;float:none;
							a {font-size:13px;color:#aaa;display:block;width:100%;font-weight:600;padding:5px 0 5px 26px;line-height:20px;white-space: nowrap;
								&:before {display:none;}
								&:hover, &:active, &:focus {color:#fff;}
							}
						}
					}
				}
			}
			ul.side {position:absolute;top:31px;right:0;
				li {display:inline-block;
					a {color:#fff;text-decoration:none;font-size:12px;display:inline-block;padding:0 6px;border-left:solid 2px #fff;
						&:hover, &:active, &:focus {text-decoration:underline;}
					}
					&:first-child a {border-left:none;}
				}
			}
		}
		&:before {content:'';display:block;width:100%;height:0;background:#000;position:absolute;left:0;top:0;@include transition(height .3s);box-shadow:$default-shadow}
		&.on {
			.inner ul.menu li ul {height:150px;@include transition(height $transitionDuration .1s);}
			&:before {height:254px;}
		}
	}


	.searchbar-wrap {width:940px;padding:0 0 20px;margin:0 auto;}
}

