//search bar
#searchBar {
	width:940px;position:relative;height:63px;
	ul.search {width:100%;height:100%;
		li {float:left;height:100%;background:#fff;position:relative;@include transition(width .3s);
			* {cursor:pointer;}
			button[type=submit] {display:block;height:100%;width:100%;border:none;color:#fff;font-weight:600;font-size:16px;background:$primary-color;outline:none;@include transition(background $transitionDuration);
				&:hover, &:focus, &:active {background:$primary-color-focus;}
			}
			&.seperator:after {content:'';display:block;position:absolute;right:0;top:18px;width:1px;height:28px;background:#e0e0e0;z-index:1000;}
			select {display:block;width:100%;height:100%;position:absolute;top:0;left:0;z-index:100;opacity:0;font-size:14px;font-weight:bold;}

			label {position:absolute;width:100%;height:100%;left:0;top:0;background:#fff;z-index:90;font-weight:bold;
				.placeholder {color:$primary-color;font-size:13px;position:absolute;top:12px;left:21px;white-space:nowrap;}
				.value {color:#000;font-size:14px;position:absolute;top:36px;left:21px;white-space:nowrap;}
				&:after {content:'';display:block;@include sprite($arrow-bottom);position:absolute;right:15px;bottom:18px;}
			}
			input[type=text] {display:block;position:absolute;color:#000;font-size:14px;z-index:100;border:none;padding:0;font-weight:bold;left:21px;top:32px;width:130px;}

			&.ptype {width:190px;}
			&.sdate {width:250px; }
			&.edate {width:250px;}
			//&.location {width:120px;overflow:hidden;}
			&.grade {width:200px;}
			&.submit {width:240px;}

			.bootstrap-datetimepicker-widget {margin-top:11px;margin-left:-21px;
				&.top {margin-bottom:35px;}
			}
		}
	}
}