html.main {
	body {padding-bottom:248px;}
	.header-wrap {box-shadow:none;position:absolute;top:0;left:0;width:100%;background:none;}

	.visual-area {height:1200px;overflow:hidden;position:relative;min-width:940px;
		.inner {width:940px;height:100%;position:relative;margin:0 auto;
			.description {width:100%;position:absolute;left:0;top:50%;z-index:100;margin-top:-200px;color:#fff;
				.title {font-size:38px;font-weight:600;margin-bottom:30px;opacity:0;@include transform(translate3d(0, -50px, 0));@include transition($material-transition, opacity .3s);@include transition-delay(.1s)}
				.area {font-size:20px;line-height:28px;opacity:0;@include transform(translate3d(0, -50px, 0));@include transition($material-transition, opacity .3s);@include transition-delay(.2s)}
			}
			#searchBar {position:absolute;left:0;top:50%;margin-top:-30px;z-index:200;@include transform(perspective(600px) rotateX(-90deg));@include transform-origin(50% 0);@include transition($material-transition);@include transition-delay(.3s);}
			.data {width:100%;position:absolute;left:0;top:50%;z-index:100;margin-top:100px;
				li {width:25%;float:left;text-align:center;color:#fff;
					.number {font-size:42px;display:block;font-weight:600;margin-bottom:20px;}
					.title {font-size:20px;display:block;}
				}
				&:after {}

			}
		}
		.visual-image {position:fixed;display:block;width:100%;height:auto;left:0;top:0;@include transition(width $transitionDuration, height $transitionDuration, left $transitionDuration, top $transitionDuration)}

		&.load {
			.inner {
				.description {
					.title, .area { @include transform(translate3d(0, 0, 0));opacity:1;}
				}
				#searchBar {@include transform(perspective(600px) rotateX(0));}
			}
		}
	}

	.service-area {height:420px;position:relative;z-index:1000;background:#fff;
		.inner {width:940px;height:100%;margin:0 auto;
			h2 {text-align:center;font-size:30px;font-weight:600;padding-top:45px;position:relative;padding-bottom:21px;
				&:after {content:'';display:block;position:absolute;width:30px;height:2px;bottom:0;left:50%;margin-left:-15px;background:$default-color;}
			}
			ul {height:180px;margin-top:49px;
				li {height:100%;width:25%;float:left;text-align:center;position:relative;padding-top:86px;
					.title {font-size:22px;font-weight:600;margin-bottom:5px;}
					.description {font-size:18px;line-height:1.44;}

					&.service1 {background:url(../imgs/rentking.w/main/ico_service1.gif) no-repeat center 8px;}
					&.service2 {background:url(../imgs/rentking.w/main/ico_service2.gif) no-repeat center 8px;}
					&.service3 {background:url(../imgs/rentking.w/main/ico_service3.gif) no-repeat center 8px;}
					&.service4 {background:url(../imgs/rentking.w/main/ico_service4.gif) no-repeat center 8px;}
				}
				&:after {content:'';display:block;float:none;clear:both;}
			}
		}

	}

	.event-area {height:420px;position:relative;z-index:1000;min-width:940px;@include transition(background .3s);background:#83add7;overflow:hidden;
		&.page-0 {background:#83add7}
		.swiper-slide {
			.coupon {
				li {position:relative;float:left;opacity:0;@include transform(scale(0));@include transition($material-transition, opacity .3s);
					&.coupon1 {@include transition-delay(.3s);}
					&.coupon2 {@include transition-delay(.4s);}
					&.coupon3 {@include transition-delay(.5s);}
				}
				&:after {content:'';display:block;clear:both;float:none;}
			}
			&.swiper-slide-active {
				.coupon {
					li.coupon1, li.coupon2, li.coupon3 {opacity:1;@include transform(scale(1));}
				}
			}
			//&.event1 {padding-top:30px;
			//	.motorshow-title1 {width:224px;height:50px;color:transparent;text-indent:-1000px;overflow:hidden;margin:0 auto;background:url(../imgs/rentking.w/main/seoul_motorshow.jpg) no-repeat}
			//	.motorshow-title2 {color:#282828;font-size:44px;font-weight:400;text-align:center;margin-top:20px;}
			//	.coupon {width:864px;margin:32px auto 0;
			//		li {width:258px;height:175px;margin:0 15px;
			//			&.coupon1 {background:url(../imgs/rentking.w/main/motorshow_coupon1.png) no-repeat center top;}
			//			&.coupon2 {background:url(../imgs/rentking.w/main/motorshow_coupon2.png) no-repeat center top;}
			//			&.coupon3 {background:url(../imgs/rentking.w/main/motorshow_coupon3.png) no-repeat center top;}
			//			div {font-size:12px;color:#414141;text-align:center;width:150px;border:solid 1px #282828;letter-spacing: -1px;line-height:21px;border-radius:11px;position:absolute;bottom:0;left:50%;margin-left:-75px;}
			//		}
			//	}
			//	.description {font-size:20px;color:#414141;text-align:center;line-height:30px;margin-top:30px;
			//		.small {font-size:16px;}
			//	}
			//
			//	@include mq(1280px) {
			//		.coupon {width:1074px;
			//			li {margin:0 50px;}
			//		}
			//	}
			//}
			&.event1 {padding-top:39px;
				.wemakeprice-title {font-size:50px;color:#fff;text-align:center;font-weight:600;line-height:50px;
					.cross {font-weight:normal;@include transform(scale(.6));display:inline-block;}
				}
				.coupon {width:894px;margin:36px auto 0;
					li {width:244px;height:155px;margin: 0 27px;
						&.coupon1 {background:url(../imgs/rentking.w/main/wemakeprice1.jpg?20170613091558) no-repeat center top;
							&:after {content:'';display:block;width:24px;height:17px;position:absolute;top:50%;margin-top:-24px;right:-40px;background:url(../imgs/rentking.w/main/wemakeprice_equal.png) no-repeat;}
						}
						&.coupon2 {background:url(../imgs/rentking.w/main/wemakeprice2.png?20170613091603) no-repeat center top;
							&:after {content:'';display:block;width:19px;height:30px;position:absolute;top:50%;margin-top:-32px;right:-40px;background:url(../imgs/rentking.w/main/wemakeprice_arrow.png) no-repeat;}
						}
						&.coupon3 {background:url(../imgs/rentking.w/main/wemakeprice3.jpg?20170613091606) no-repeat center top;}
						div {font-size:13px;color:#225b92;text-align:center;position:absolute;bottom:0;width:100%;font-weight:600;}
					}
				}
				.description {color:#fff;text-align:center;font-size:30px;line-height:40px;margin-top:32px;}

				@include mq(1280px) {
					.coupon {width:1074px;
						li {margin:0 57px;
							&.coupon1 {
								&:after {right:-70px;}
							}
							&.coupon2 {
								&:after {right:-70px;}
							}
						}
					}
				}
			}
		}

		button.swiper-button-prev, button.swiper-button-next {outline:none;position:absolute;z-index:100;top:50%;left:50%;margin-top:-13px;display:block;width:25px;height:44px;background:url(../imgs/rentking.w/main/arrow.png) no-repeat;border:none;color:transparent;text-indent:-100px;overflow:hidden;}
		button.swiper-button-prev {margin-left:-470px;
			@include mq(1280px) {margin-left:-600px;}
			&:hover, &:active, &:focus {background-position:0 -44px;}
		}
		button.swiper-button-next {background-position:-25px 0;margin-left:445px;
			@include mq(1280px) {margin-left:575px;}
			&:hover, &:active, &:focus {background-position:-25px -44px;}
		}

		.swiper-pagination {position:absolute;bottom:20px;right:10px;text-align:right;padding-right:20px;
			.swiper-pagination-bullet {margin:0 4px;width:14px;height:14px;border:solid 1px #fff;border-radius:7px;background:none;position:relative;opacity:1;
				&:after {content:'';position:absolute;left:-1px;top:-1px;width:14px;height:14px;background:#fff;border-radius:7px;@include transform(scale(0));@include transition(transform .3s);}
				&.swiper-pagination-bullet-active:after {@include transform(scale(1));}
			}
		}
	}

	.rentkingLoading {background:#000;position:fixed;top:0;left:0;}
}